import React, { useState, useEffect } from "react"
import * as S from "./styles"
import { ThemeProvider } from "styled-components"
import { GlobalStyles } from "globalStyles"
import { lightTheme, darkTheme } from "globalStyles/theme"
import Sidebar from "projectOrganisms/Sidebar"
import MainNav from "projectOrganisms/MainNav"
import { setToLS, getFromLS } from "globalLibs/helpers"
import { window, document } from "browser-monads"

export default function Layout(props) {
  let themeSwitchButton
  const isBrowser = typeof window !== "undefined"

  let defaultTheme = "light"
  let userTheme = defaultTheme

  isBrowser && getFromLS("theme") === undefined
    ? setToLS("theme", defaultTheme)
    : null

  isBrowser && getFromLS("theme") !== undefined
    ? (userTheme = getFromLS("theme").replace(/"/g, ""))
    : null

  const [theme, setTheme] = useState(userTheme)

  theme === "light" ? setToLS("theme", "light") : setToLS("theme", "dark")

  const handleChangeTheme = () =>
    theme === "light" ? setTheme("dark") : setTheme("light")

  useEffect(() => {
    if (!document.getElementById("theme-toggler")) return false
    themeSwitchButton = document.getElementById("theme-toggler")
    themeSwitchButton.addEventListener("click", handleChangeTheme)
    return () => {
      themeSwitchButton.removeEventListener("click", handleChangeTheme)
    }
  }, [theme])

  return (
    <ThemeProvider theme={theme === "light" ? lightTheme : darkTheme}>
      <GlobalStyles />
      <S.Layout
        as="main"
        className={`${props.className} d-print-none`}
        id={props.id}
      >
        <Sidebar />
        <MainNav></MainNav>
        <div className="main-content mt-48 mb-56">{props.children}</div>
      </S.Layout>
    </ThemeProvider>
  )
}
